/* Register the block */
import { registerBlockType } from '@wordpress/blocks';
import { useSelect } from '@wordpress/data';

registerBlockType( 'u24web/card-body', {
    title: 'Card body',

    edit: function(props) {
        const postId = useSelect(
            (select) => select('core/editor').getCurrentPostId(),
            []
        );

        const postData = useSelect(
            (select) => select('core').getEntityRecord('postType', 'post', postId),
            [postId]
        );

        const postCategories = useSelect(
            (select) => select('core').getEntityRecords('taxonomy', 'category', { post: postId }),
            [postId]
        );

        if (!postData || !postCategories) return null;

        const postDate = new Date(postData.date);

        return (
            <div className="card-body pb-4">
                Hello
                <div className="d-flex align-items-center justify-content-between mb-3">
                    <a
                        href="#"
                        className="badge fs-sm text-nav bg-secondary text-decoration-none"
                    >
                        {postCategories[0].name}
                    </a>
                    <span className="fs-sm text-muted">{postDate.toDateString()}22</span>
                </div>
                <h3 className="h5 mb-0">
                    <a href={"blog-single.html"}>{postData.title.rendered}</a>
                </h3>
            </div>
        );
    },

    save: function(props) {
        return null;
    },
});