/*Rejestracja statycznego contentu */
import { registerBlockType } from '@wordpress/blocks';
import { PlainText } from '@wordpress/block-editor';

import { useSelect } from '@wordpress/data';

registerBlockType( 'u24web/card-footer', {


    title: 'Card footer',

    attributes: {
        textContent: {
            type: 'string',
            value: '',
            default: 'OPERACJA FURIA'
        },
    },
    edit: function(props) {
        const authorId = useSelect(
            (select) => select('core/editor').getEditedPostAttribute('author'),
            []
        );
    
        const authorData = useSelect(
            (select) => select('core').getEntityRecord('root', 'user', authorId),
            [authorId]
        );
    
        if (!authorData) return null;
    
        return (
            <div className="card-footer py-4">
                <a
                    href="#"
                    className="d-flex align-items-center fw-bold text-dark text-decoration-none"
                >
                    <img
                        src={authorData.avatar_urls[48]}
                        className="rounded-circle me-3"
                        width={48}
                        alt="Avatar"
                    />
                    {authorData.name}
                </a>
            </div>
        );
    },
    save: function(props) {
        return null;
    },
} );