/* Register the block */
import { registerBlockType } from '@wordpress/blocks';
import { useSelect } from '@wordpress/data';

registerBlockType( 'u24web/card-image', {
    title: 'Card image',

    edit: function(props) {
        const featuredMediaId = useSelect(
            (select) => select('core/editor').getEditedPostAttribute('featured_media'),
            []
        );

        const featuredMedia = useSelect(
            (select) => select('core').getEntityRecord('root', 'media', featuredMediaId),
            [featuredMediaId]
        );

        if (!featuredMedia) return null;

        return (
            <div className="position-relative">
                herro
                <a
                  href="blog-single.html"
                  className="position-absolute top-0 start-0 w-100 h-100"
                  aria-label="Read more"
                />
                <a
                  href="#"
                  className="btn btn-icon btn-light bg-white border-white btn-sm rounded-circle position-absolute top-0 end-0 zindex-5 me-3 mt-3"
                  data-bs-toggle="tooltip"
                  data-bs-placement="left"
                  title="Read later"
                >
                  <i className="bx bx-bookmark" />
                </a>
                {featuredMedia.source_url}
                <img src={featuredMedia.source_url} className="card-img-top" alt="Image" />
            </div>
        );
    },

    save: function(props) {
        return null;
    },
} );
