import { registerBlockType } from '@wordpress/blocks';
import { useBlockProps, RichText, QueryControls, QueryPagination, InnerBlocks } from '@wordpress/block-editor';
const MY_TEMPLATE = [
	["core/query", {
        query: {
            per_page: 5,
            order: 'desc',
            orderby: 'date',
        },
        displayLayout: {
            type: 'list',
        },
    },], 	["core/image"]
];
// const DEFAULT_QUERY = {
//     per_page: 3,
//     pages: 1,
//     offset: 0,
//     post_type: 'post',
//     order: 'desc',
//     orderby: 'date',
// };

registerBlockType('u24web/loop', {
    title: 'loop Portfolio',

    attributes: {
        textContent: {
            type: 'string',
            value: '',
            default: 'Portfolioso Grid'
        },
    },
    edit: function (props) {
        return (
            <>
                <section className="">
                    <RichText
                        tagName="h1"
                        className="text-nowrap mb-md-4 pe-md-5"
                        placeholder="Portfolios Grid"
                        value={props.attributes.textContent}
                        onChange={(value) => props.setAttributes({ textContent: value })}
                    />
                </section>
                <section className="container">
                    <div className="row">
                        <InnerBlocks   template={MY_TEMPLATE} allowedBlocks={['core/query']} />
                    </div>
                </section>
            </>
        );
    },
    save: function (props) {
        return (
            <>
                <section className="">
                    <RichText.Content
                        tagName="h1"
                        className=""
                        value={props.attributes.textContent}
                    />
                </section>
                <section className="container">
                    <div className="row">
                        <InnerBlocks.Content />
                    </div>
                </section>
            </>
        );
    },
});